<template>
  <div class="swaps-comments">
    <div class="swaps-comments__filters">
      <SearchInput v-model="filters.search" />
      <DateRange v-model="filters.dateRange" />
    </div>
    <div class="swaps-comments__body">
      <Text variant="h3" size="20px" line-height="23px" weight="500" mb="20px">
        Comments
      </Text>
      <Input
        v-model="comment"
        hide-shadow
        bordered
        placeholder="Add a comment"
        class="swaps-comments__add-input"
      />
    </div>
  </div>
</template>

<script>
import { useGetters } from '@/shared/plugins/vuex'
import { computed, ref } from 'vue'
import { MODULES_NAMES } from '@/shared/common/modules'
import Text from '@/shared/components/UIKit/Text/Text'
import Input from '@/shared/components/UIKit/Input'
import DateRange from '@/shared/components/UIKit/Date/DateRange'
import SearchInput from '@/admin/components/Inputs/SearchInput'

export default {
  name: 'SwapsComments',
  components: { Input, Text, DateRange, SearchInput },
  setup () {
    const {
      getComments: comments
    } = useGetters(MODULES_NAMES.SWAPS)

    const comment = ref('')
    const filters = computed({
      get: () => comments.value.filters,
      set: (val) => {
        comments.value.filters = val
      }
    })

    return {
      comment,
      filters,
      comments
    }
  }
}
</script>

